  }
}
@media (min-width: 120em) {
  .product__features-wrap {
    gap: 3.2rem;
  }
}
.product__feature {
  padding: 2.4rem 2.4rem 2.2rem 2.4rem;
  background-color: var(--color-bg-secondary);
  border-radius: 1.6rem;
  position: relative;
  min-height: 21rem;
}
@media (min-width: 30em) {
  .product__feature {
    border-radius: 3.2rem;
    padding: 3.2rem;
    min-height: 32.8rem;
  }
}
@media (min-width: 48em) {
  .product__feature {
    min-height: 65.8rem;
    flex: 1 1 0;
  }
}
@media (min-width: 120em) {
  .product__feature {
    padding: 6.4rem;
    min-height: 61.6rem;
  }
}
.product__feature-list {
  position: absolute;
  margin-top: 1.3rem;
  z-index: 2;
  list-style: none;
}
.product__feature-img {
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
}

.footer {
  padding: 5.5rem 0;
  background-color: var(--color-bg-tertiary);
}
@media (min-width: 30em) {
  .footer {
    padding: 6.3rem 0 4.8rem 0;
  }
}
@media (min-width: 48em) {
  .footer {
    padding: 7.95rem 0 6.4rem 0;
  }
}
.footer__wrap {
  display: flex;
  gap: 1.2rem;
}
.footer__copy {
  margin-top: 0.05rem;
}
.footer__info {
  margin-top: 0.8rem;
}
@media (min-width: 30em) {
  .footer__info {
    margin-top: 1.2rem;
  }
}
@media (min-width: 48em) {
  .footer__info {
    width: 30%;
  }
}

.buy-btn {
  display: inline-block;
  padding: 1.5rem 3.2rem;
  height: 4.8rem;
  background-color: var(--color-primary-active);
  color: var(--color-text-tertiary);
  font-family: "Roboto";
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.8rem;
  border-radius: 0.5rem;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.2s;
}
.buy-btn:hover {
  background-color: var(--color-primary-hover);
}

.disc-img {
  display: flex;
  width: 100%;
  position: relative;
}
.disc-img__img {
  display: block;
  width: 100%;
  height: auto;
  z-index: 2;
  object-fit: contain;
}
.disc-img__discount {
  position: absolute;
  right: 4.8%;
  top: 56%;
